<template>
  <div>
    <div class="accordion-item">
      <h2 class="accordion-header" :id="'heading' + moveId">
        <button
          class="accordion-button collapsed"
          type="button"
          data-bs-toggle="collapse"
          :data-bs-target="'#collapse' + moveId"
          aria-expanded="false"
          :aria-controls="'collapse' + moveId"
        >
          {{ moveName }}
        </button>
      </h2>
      <div
        :id="'collapse' + moveId"
        class="accordion-collapse collapse"
        :aria-labelledby="'heading' + moveId"
        :data-bs-parent="'#accordion' + categoryId"
      >
        <div
          class="accordion-body"
          style="text-align: left"
          v-html="content"
        ></div>
        <roll-table v-if="moveData['Table']" :ChanceArray="getTable" StartValue="1" TableName=""/>
        <roll-table-multi v-if="moveData['TableMulti']" :data="moveData['TableMulti']"/>
      </div>
    </div>
  </div>
</template>

<script>
import RollTable from './RollTable.vue';
import RollTableMulti from './RollTableMulti.vue';
export default {
    components: { RollTable, RollTableMulti },
  name: "Move",
  props: {
    moveName: String,
    categoryId: String,
    moveData: Object
  },
  computed: {
    content() {
      var md = require("markdown-it")();
      return md.render(this.$props.moveData["Text"]);
    },
    moveId() {
      return this.$props.moveName.replace(/\s+/g, "").replace(/[()]/g, "");
    },
    getTable() {
      var tableData = this.$store.state;
      for (var i = 0; i < this.$props.moveData['Table'].length; i++) {
        tableData = tableData[this.$props.moveData['Table'][i]];
      }
      return tableData;
    }
  }
};
</script>
