<template>
  <div>
    <div style="display: flex">
      <button type="button" class="btn btn-primary flex-fill" v-on:click="rollTable()">
        {{output}}
      </button>
    </div>
    <table class="table">
      <thead>
        <tr>
          <th scope="col"> # </th>
          <th scope="col">Result</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(value, index) in ChanceArray" v-bind:key="index">
          <th scope="row" class="col-md-2">
            {{ getOdds(ChanceArray, index) }}
          </th>
          <td style="text-align: left" v-html="require('markdown-it')().render(value['Description'])"></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "RollTable",
  props: ["TableName", "ChanceArray", "StartValue"],
  methods: {
    getOdds(oddsArray, index) {
      if (index == 0) return parseInt(this.$props['StartValue']) +" - " + oddsArray[index]["Chance"];

      return (
        oddsArray[index - 1]["Chance"] + " - " + oddsArray[index]["Chance"]
      );
    },
    getRandomInt(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min) + min); //The maximum is exclusive and the minimum is inclusive
    },
    rollTable() {
      var r = this.getRandomInt(1, 100);
      console.log(this.$props.ChanceArray);
      for (var i = 0; i < this.$props.ChanceArray.length; i++) {
        if (r <= this.$props.ChanceArray[i]["Chance"]) {
          this.output = "...";
          setTimeout(() => {
            this.output = "["+r+"] " +this.$props.ChanceArray[i]["Description"];
          }, 100)
          
          return;
        }
      }
    }
  },
  computed: {
    getId() {
      return this.$props.TableName.replace(/\s+/g, "").replace(/[()]/g, "");
    }
  },
  data: function() {
    return {
      output: "Roll"
    }
  }
};
</script>
