<template>
    <div id="root">
        <select class="form-select" v-model="option">
            <option selected value="none">Select an option...</option>
            <option v-for="(index, propname) in data" :key="propname" :value="propname">{{propname}}</option>
        </select>
        <roll-table v-if="option != 'none'" StartValue="1" TableName="" :ChanceArray="getTable"/>
    </div>
</template>

<script>
import RollTable from './RollTable.vue'
export default {
    name: "RollTableMulti",
    props: ['data'],
    components: { RollTable },
    data: function() {
        return {
            option: "none"
        }
    },
    computed: {
        getTable() {
            var tableData = this.$store.state;
            for (var i = 0; i < this.$props.data[this.option].length; i++) {
                tableData = tableData[this.$props.data[this.option][i]];
            }
            return tableData;
            
        }
    }
}
</script>

<style scoped>
#root {
    padding: 10px;
}
</style>