<template>
  <div class="card">
    <h3 class="card-header">{{ categoryName }}</h3>
    <div class="card-body">
      <div class="accordion" :id="'accordion' + categoryId">
        <move
          v-for="(value, propertyName) in moves[categoryName]"
          v-bind:key="propertyName"
          :move-name="propertyName"
          :move-data="value"
          :category-id="categoryId"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Move from "./Move";

export default {
  name: "MoveCategory",
  props: ["categoryName"],
  components: {
    Move
  },
  computed: {
    moves() {
      return this.$store.state.Moves;
    },
    categoryId() {
      return this.$props.categoryName.replace(/\s+/g, "").replace(/[()]/g, "");
    }
  }
};
</script>
